<template >
  <div class="textBox" style="height:900px;">
    <el-form ref="form" :rules="rules" :model="form" label-width="150px" >
      <el-row>
        <el-col :span="8">
          <el-form-item
          prop="commandId"
          label="栏目选择">
          <el-select
            @change="Selector"
            style="width:150px"
            v-model="form.commandId"
            filterable
            allow-create
            placeholder="请选择标签">
            <el-option
              v-for="item in articleList"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        </el-col>
        <el-col :span="8" v-show="isShow">
          <el-form-item
            label="编辑者"
            prop="author">
            <el-input v-model.number="form.author" style="width:150px"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8" v-show="isShow">
          <el-form-item
            label="发布时间"
            prop="reporttime">
            <el-date-picker
              placement="top"
              v-model="form.reporttime"
              type="datetime"
              placeholder="选择日期时间"
              align="right"
              :picker-options="pickerOptions">
            </el-date-picker>
          </el-form-item>
        </el-col>

      </el-row>
      <el-form-item
        label="标题"
        prop="title" v-show="isShow">
        <el-input v-model.number="form.title" style="width:1000px"></el-input>
      </el-form-item>
      <el-form-item

        prop="imagepath"
        label="首页图片" v-show="isShow">
        <div style="width:1000px;background-color: #fff;text-align: center;">
        <el-upload
          action=""
          list-type="picture-card"
          :file-list="fileList"
          :on-preview="handlePictureCardPreview"
          :auto-upload="false"
          :on-remove="handleRemove"
          :on-change="uploadImageSetter"
          :limit="1"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog style="width: 800px;height: 300px"  :visible.sync="logVisible">
          <img :src="dialogImageUrl" alt="">
        </el-dialog>
        </div>
      </el-form-item>
      <el-form-item
        label="发布内容"
      >
        <form name="form1">
          <input type="file" ref="myfile" name="picpath" id="picpath" style="display:none;" onChange="document.form1.path.value=this.value">
          <input name="path" readonly>
          <input type="button" value="插入本地图片" onclick="document.form1.picpath.click()">
        <el-button @click="importlocalImg" type="success" icon="el-icon-upload2" size="mini">确定</el-button>
        <el-button @click="submitArticle" size="mini">立即创建</el-button>
        <el-button size="mini">取消</el-button>
          <el-button @click="getContent" size="mini">获取内容</el-button>
        </form>
        <UEditor :config=config ref="ueditor" style="width:1000px"></UEditor>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>

   /**
   * Ueditor配置项文件static/ueditor/ueditor.config.js
   * 生产环境下需按后台配置更改服务器统一请求接口路径serverUrl（已解决后台配置项未成功加载问题）
   */

   import UEditor from '@/components/ueditor/ueditor.vue'
   import config from '@/api/config'
   import Cookies from 'js-cookie'
   import axios from 'axios';
   axios.defaults.withCredentials = true
  export default{
    name: 'atricle',
    components: {UEditor},
    data(){
      return {
        //保存首页图片
        imgFormData:null,
        catchnewsId:null,
        postId:null,
        resId:null,
        isShow:true,

        // 日期控件
        pickerOptions: {
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value1: '',
        value2: '',
        value3: '',
        config: {
          //可以在此处定义工具栏的内容
          autoHeightEnabled: false,
          autoFloatEnabled: true,
          initialContent:'请输入内容',   //初始化编辑器的内容,也可以通过textarea/script给值，看官网例子
          autoClearinitialContent:true, //是否自动清除编辑器初始内容，注意：如果focus属性设置为true,这个也为真，那么编辑器一上来就会触发导致初始化的内容看不到了
          initialFrameWidth: null,
          initialFrameHeight: 450,
          BaseUrl: '',
          UEDITOR_HOME_URL: 'static/ueditor/'
        },
        mounted:{

        },
        addFormVisible: false,
        articleList:[
          {name:"新增-新闻内容",id:10},
          {name:"编辑-区域优势",id:0},
          {name:"编辑-自然环境优势",id:1},
          {name:"编辑-生态优势",id:2},
          {name:"编辑-文化优势",id:3},
          {name:"编辑-科技优势",id:4},
          ],
            sels: [],//选中显示的值
            fileList:[],
            cntList:[],
            imgList:[],
            dialogImageUrl: '',
            logVisible: false,
            userList:[],
            imageUri:'',
            tableData: [],
            tableData1:[],
            tableHeader1: ['企业名称', '地址','所属行业','主要经营品种'],
            form: {},
            rules: {
              commandId: [
                {required: true, message: '输入不能为空'},
              ],
              author: [
                {required: true, message: '输入不能为空'},
              ],
              title: [
                {required: true, message: '输入不能为空'},
              ],
              reporttime: [
                {required: true, message: '输入不能为空'},
              ],
              imagepath: [
                {required: true, message: '输入不能为空'},
              ]
            }
      }
    },
    methods: {
      /**
       *栏目管理功能列表
       * 1.栏目选择器
       * 2.栏目编辑器
      */

      /**
       *栏目选择器
       * @param vl
       * @constructor
       */
      Selector:function(vl) {
        if(vl===10){
          this.Monitor(true);
          this.postId=vl;
        }
        else{
          this.Monitor(false);
          this.postId=vl;
         // this.Reporter("other",vl);
        }
      },
      //显示器
      Monitor:function(vl){
        this.isShow=vl;
      },

      /**
       *2.栏目编辑器
       */
      //提交编辑管理
      submitArticle:function(){
        var self=this;
        if(!self.postId&&self.postId!==0){
          self.Reporter('other','请选择栏目');
          return;
        }
        else if(self.postId===10){
          self.$confirm('此操作将编辑并发布新闻, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning',
            center: true
          }).then(() => {
            self.addNews();
          }).catch(() => {
            self.Reporter("cancel");
          });
        }
        else{
          self.$confirm('此操作将修改对应的文章, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning',
            center: true
          }).then(() => {
            self.updateIntroduction();
          }).catch(() => {
            self.Reporter("cancel");
          });
        }
      },
      //修改园区介绍信息
      updateIntroduction:function(){
        var self=this;
        var param={
          ad_id:self.postId,
          content:self.$refs.ueditor.getUEContent()
        }
        axios.defaults.withCredentials = true;
        axios.post("http://120.79.185.173:8080/agriculture/backend/news/updateIntrodution.do", param)
          .then((res)=>{
            //console.log(res.data);
            if(res.data.status===0){
              self.Reporter("success");
            }
            else{
              self.Reporter("other",res.data.data.msg);
            }
          })
          .catch((error)=>{
            self.Reporter("error");
          })
      },
      //添加新闻
      addNews:function() {
        var self = this;
        //调用methods下的timestampToTime方法,转换yyyy-MM-dd hh:mm:ss
        var time = self.timestampToTime(self.form.reporttime, true);
        var param = {
          content: self.$refs.ueditor.getUEContent(),
          author: self.form.author,
          reporttime: time,
          title: self.form.title
        }
        axios.post(config.rootURL + "backend/news/add.do", param)
          .then((res) => {
            console.log(res);
            if (res.data.status === 0) {
              console.log(res.data);
              self.Reporter("success");
              self.resId = res.data.data.id
              console.log(self.resId);
              self.uploadImg();
            }
          })
          .catch(() => {
            self.Reporter("error");
          })
      },
      //添加首页图片
      uploadImg:function(){
        var self=this;
        var mid=self.resId;
        var config = {
          headers: {'Content-Type': 'multipart/form-data'}
        };
        if(!self.imgFormData){
          self.Reporter('other',"请先上传图片")
        }
        console.log(mid);
        console.log(self.imgFormData);
        axios.post("http://120.79.185.173:8080/agriculture/backend/news/addNewsImage.do?id="+mid,self.imgFormData,config)
          .then((res)=>{
            console.log(res);
            self.resId=null;
          })
          .catch((error)=>{
            console.log("error");
            self.Reporter("error");
          })
      },
      //上传图片暂存器
      uploadImageSetter:function(file, fileList){
        var self=this;
        var formData=new FormData();
        formData.append("file",file.raw);
        console.log(formData);
        self.imgFormData=formData;//上传前保存图片数据
        console.log(self.imgFormData);
      },
      //信息提示器
      Reporter:function(e,msg){
        switch(e){
          case 'cancel':
            this.$message({
              type: 'info',
              message: '取消操作'
            });
            break;
          case 'success':
              this.$message({
                type: 'success',
                message: '操作成功'
              });
            break;
          case 'error':
            this.$message({
              type: 'warning',
              message: '错误操作'
            });
            break;
          case 'other':
            this.$message({
              type: 'info',
              message: msg
            });
            break;
          default:
            this.$message({
              type: 'info',
              message: '异常操作'
            });
        }
      },
      //本地图片插入器
      importlocalImg:function() {
        let myfile = this.$refs.myfile;
        let files = myfile.files;
        let file = files[0];
        var that=this;
        let config = {
          headers: {'Content-Type': 'multipart/form-data'}
        };
        const formData=new FormData();
        formData.append("upfile",file);
        console.log(file);
        console.log(formData);
        axios.post(config.rootURL+'agriculture/uploadImage',formData,config)
          .then((res) => {
            console.log(res.data.data.url);
            var cnt=res.data.data.url;
            that.$refs.ueditor.setUEContent(cnt);

          })
          .catch((error) =>{
            console.log("error");
          })
      },
      /**
       * 将时间戳或者中国标准时间处理成 2018-05-01 00:00:00  这种格式
       * @param {时间戳或者中国标准时间} timestamp
       * @param {一状态 } state   ture要时分秒  false不要时分秒
       */
      //时间转换器
      timestampToTime:function(timestamp,state) {
        var date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1)+ '-';
        var D = date.getDate()+ ' ';
        var h = date.getHours()+ ':';
        var m = date.getMinutes()+ ':';
        var  s = date.getSeconds();
        return state?Y+M+D+h+m+s:Y+M+D;
      },
      //获取发布内容代码
      getContent: function(){
        var content = this.$refs.ueditor.getUEContent();
        console.log(content);
        alert(content);
      },
      //其他
      handleRemove:function(file, fileList) {

      },
      handlePictureCardPreview:function(file) {

      },
    }
  }

</script>
<style scoped>
  .textBox{
    background-color:#EDEDED ;
  }
</style>



